/* eslint-disable camelcase */
/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import { CirclePicker } from 'react-color';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  CustomInput,
  FormGroup,
  Input,
  InputGroup,
  InputGroupAddon,
  Row,
} from 'reactstrap';
import { useHistory } from 'react-router-dom';

import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import { adminRoot } from '../../../constants/defaultValues';

import Breadcrumb from '../../../containers/navs/Breadcrumb';
import AppError from '../../../helpers/AppError';

import IntlMessages from '../../../helpers/IntlMessages';
import instance from '../../../helpers/axiosInstance';
import StateButton from '../../../components/StateButton';

const Brands = ({ match }) => {
  const { id } = match.params;

  if (!id) throw new AppError('Please provide a Hackathon ID');

  const history = useHistory();

  const [values, setValues] = useState({
    id,
    theme_color_code: null,
    hackathon_logo: null,
    hackathon_cover_img: null,
  });

  const [colorHexCode, setColorHexCode] = useState('#000000');
  const formData = new FormData();
  formData.append('id', id);

  const handleChange = (event, key, value = false) => {
    setValues({
      ...values,
      theme_color_code: value ? event : event.target.value,
    });
  };

  const handleFileChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.files[0] });
  };

  const handleUpload = async (event) => {
    formData.delete(event.target.id);

    formData.append(`${event.target.id}`, values[event.target.id]);

    await instance({
      url: '/updateHackathon',
      method: 'POST',
      headers: {
        'Content-Type': 'multipart/form-data',
      },
      data: formData,
      timeout: 100000,
    });
  };

  const handleSubmit = async (newData) => {
    return new Promise((success, failed) => {
      const updateProfile = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/updateHackathon',
            data: { id, ...newData },
          });

          const { data } = response;
          if (data.success) {
            success('Hackathon updated successfully!');
          } else {
            success('Hackathon updated successfully!');
          }
        } catch (error) {
          failed('Failed to update hackathon!');
        }
      };
      updateProfile();
    });
  };

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.brands" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row className="justify-content-center">
        <Colxx xxs="12" md="10" className="mb-4">
          <Card className="mb-4">
            <CardBody>
              <CardTitle>
                <div className="lead h4">
                  <IntlMessages id="challenges.theme-title" />
                </div>
              </CardTitle>

              <FormGroup className="mb-6">
                <Input value={colorHexCode} disabled />
              </FormGroup>

              <FormGroup className="mb-6 p-2">
                <CirclePicker
                  width="100%"
                  color={colorHexCode}
                  onChange={(event) => {
                    setColorHexCode(event.hex);
                    handleChange(event.hex, 'theme_color_code', true);
                  }}
                />
              </FormGroup>
            </CardBody>
          </Card>

          <Card className="mb-4">
            <CardBody>
              <CardTitle>
                <div className="lead h4">
                  <IntlMessages id="challenges.hackthon-logo-title" />
                </div>
              </CardTitle>

              <div className="form-group mb-6">
                <InputGroup>
                  <CustomInput
                    type="file"
                    id="hackathon_logo"
                    name="customFile"
                    accept="image/jpeg, image/jpg, image/png"
                    onChange={handleFileChange}
                  />
                  <InputGroupAddon addonType="append">
                    <Button
                      outline
                      color="secondary"
                      onClick={handleUpload}
                      id="hackathon_logo"
                    >
                      <IntlMessages id="challenges.upload-button" />
                    </Button>
                  </InputGroupAddon>
                </InputGroup>
              </div>
            </CardBody>
          </Card>

          <Card className="mb-4">
            <CardBody>
              <CardTitle>
                <div className="lead h4">
                  <IntlMessages id="challenges.hackthon-cover-image-title" />
                </div>
              </CardTitle>

              <div className="form-group mb-6">
                <InputGroup>
                  <CustomInput
                    type="file"
                    id="hackathon_cover_img"
                    name="customFile"
                    accept="image/jpeg, image/jpg, image/png"
                    onChange={handleFileChange}
                  />
                  <InputGroupAddon addonType="append">
                    <Button
                      outline
                      color="secondary"
                      id="hackathon_cover_img"
                      onClick={handleUpload}
                    >
                      <IntlMessages id="challenges.upload-button" />
                    </Button>
                  </InputGroupAddon>
                </InputGroup>
              </div>
            </CardBody>
          </Card>

          <FormGroup className="text-center mb-4">
            <StateButton
              id="submitLinks"
              color="primary"
              type="submit"
              onClick={() => handleSubmit(values)}
            >
              <IntlMessages id="forms.update" />
            </StateButton>
          </FormGroup>
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              onClick={() =>
                history.push(`${adminRoot}/challenges/links/${id}`)
              }
            >
              <IntlMessages id="forms.previous" />
            </Button>
            <Button
              color="primary"
              className="m-3"
              onClick={() =>
                history.push(`${adminRoot}/challenges/dates/${id}`)
              }
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

export default Brands;
